<template>
  <div class="side-nav">
    <el-menu
      :default-active="defaultActiveIndex"
      class="el-menu-vertical-demo"
      background-color="#f0f9eb"
      text-color="#88CC96"
      active-text-color="#DC8A8A"
    >
      <el-submenu
        :index="'' + index"
        v-for="(item, index) in sideNavMenu"
        :key="index"
      >
        <template slot="title">
          <span>{{ item.title }}</span>
        </template>
        <template v-if="item.children && item.children.length">
          <el-menu-item
            v-for="(child, childIndex) in item.children"
            :key="childIndex"
            :index="index + '-' + (childIndex+1)"
            @click="checkMenuItem(child)"
            >{{ child.title }}</el-menu-item
          >
        </template>
      </el-submenu>
    </el-menu>

    <i class="arrow"></i>
  </div>
</template>
<script>
export default {
  data() {
    return {
      defaultActiveIndex: "0-1",
    };
  },
  props: {
    sideNavMenu: {
      type: Array,
      default() {
        return [];
      },
    },
  },
  mounted() {
    this.defaultActiveIndex = this.$route.meta.index
  },
  methods: {
    checkMenuItem(item) {
      if (item) {
        this.$router.push({ path: item.path});
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.side-nav {
  width: 300px;
  background-color: $self-nav-color;
  position: relative;
  min-height: calc(100vh - 80px);
  .arrow {
    display: inline-block;
    position: absolute;
    width: 16px;
    height: 16px;
    right: 0;
    &-left {
    }
  }
  .el-menu {
    // border: 1px solid rgba(136, 204, 150, 0.5);
    border: none;
  }
}
</style>